import React from 'react';
import Loadable from 'react-loadable'
import ProductList from './components/product/ProductList';
import ProductCategory from './components/product/ProductCategory';
import EditProduct from './components/product/EditProduct';
import Profile from './components/profile/Profile';
import ChangePassword from './components/auth/ChangePassword';
import UserList from './components/admin-dashboard/manage-users/UserList';
import LocationDetail from './components/location/LocationDetail';
import ReportList from './components/admin-dashboard/manage-reports/ReportList';
import PostCategory from './components/admin-dashboard/manage-category/PostCategory';
import LocationCategory from './components/admin-dashboard/manage-category/LocationCategory';
import LocationAdmin from './components/admin-dashboard/manage-location/LocationAdmin';
import AddLocation from './components/admin-dashboard/manage-location/AddLocation';
import ChangePasswordAdmin from './components/admin-dashboard/ChangePasswordAdmin';

function Loading() {
  return <div>Loading...</div>;
}
const AddProduct = Loadable({
    loader: () => import('./components/product/AddProduct'),
    loading: Loading,
  });
  
const routes = [
    { path: '/', exact: true, name: 'Trang Ch', component: ProductList },
    { path: '/product/add', exact: true, name: 'Thm sn phm', component: AddProduct },
    { path: '/product/edit', exact: true, name: 'Sa sn phm', component: EditProduct },
    { path: '/category', exact: true, name: 'Qun l th loi', component: ProductCategory },
    { path: '/product', exact: true, name: 'Qun l sn phm', component: ProductList },
    { path: '/profile', exact: true, name: 'Thng tin c nhn', component: Profile },
    { path: '/chgpwd', exact: true, name: 'Thay i mt khu', component: ChangePassword },
    { path: '/locationDetail', exact: true, name: 'Thng tin a im', component: LocationDetail },
    { path: '/admin/allusers', exact: true, name: 'Tt c ngi dng', component: UserList },
    { path: '/admin/report', exact: true, name: 'Bo co', component: ReportList },
    { path: '/admin/category/location', exact: true, name: 'Th loi a im', component: LocationCategory },
    { path: '/admin/category/post', exact: true, name: 'Th loi bi vit', component: PostCategory },
    { path: '/admin/location', exact: true, name: 'Qun l a im', component: LocationAdmin },
    { path: '/admin/location/add', exact: true, name: 'Thm ia im mi', component: AddLocation },
    { path: '/admin/chgpwd', exact: true, name: 'Thay i mt khu', component: ChangePasswordAdmin },
]
export default routes;